         )
        source(
          src="../../../assets/images/works_img/ui/game/video_1.mp4"
          type="video/mp4"
          )
      el-col(
        :sm="12"
      )
        h4 ,,
        h4  slot game, play-flow , game , event ,, data-flow 

</template>

<script>

import pageShareTitle from '../../../components/PageShareTitle'


export default {
  name: 'game',
    components: {
      pageShareTitle
    },
    data () {
    return {
   
    }
  },
  computed: {
    
  },
  mounted () {

    
},
  methods: {


  },
  watch: {

  }
}
