          box-shadow: 5px 5px 6px 1px #c1c1c1
          -webkit-box-shadow: 5px 5px 6px 1px #c1c1c1
          -moz-box-shadow: 5px 5px 6px 1px #c1c1c1
          -o-box-shadow: 5px 5px 6px 1px #c1c1c1
        h4
          width: 60%
          margin: 0px 0px 10px 20px
      .video-box
        text-align: end
    .landscape.el-row
      .el-col
