          width: 400px

  @media screen and (max-width: 1000px)
    #game
      .landscape.el-row
        .el-col
          video
            width: 300px
    
  @media screen and (max-width: 800px)
    #game
      .page-share-title
      .el-row
        .el-col
          h4
            margin: 50px auto 20px
        .video-box
          text-align: center
      .landscape.el-row
        .el-col
          video
            width: 200px

  @media screen and (max-width: 430px)
    @media (orientation: portrait) //  , landscape 
      #game
        .top-introduction
          h4,h2
            width: 80%
        .el-row
          .el-col
            h4
              width: 80%

</style>



