<style lang="sass">

@import '../../../assets/styles/var'

$fontColor: #a8a8a8
$BGColor: #141516
$focusColor: #2bdeb6

#movie-store
  color: $fontColor
  div
  .top-flow-img-box
    text-align: center
    padding: 10vh 0vw
    img
      width: 60%
  p
    @include pStyle(60%)
    margin: 50px auto
  .introduction
  .order-way
    background-color: #1d1d1d
    padding: 20px 0px 20px 0px
  .view-box
    position: relative
    width: 100%
    .content-box
      width: 60%
      margin: 10vh auto
      display: flex
      .img-box
        z-index: 1000
        width: 30%
        img
          width: 100%
      .text-box
