          width: 80%
  .view-box.bigger.center
    .content-box
      .text-box
        .p-box
          .line
            width: 140%
          .BG-Block
            height: 235px
            left: -45%
  .bottom
    margin: 10vh auto 0vh
    padding-bottom: 10vh
    .content-box
      .text-box
        .p-box
          .line
            width: 200%
          .BG-Block
            height: 115px
            left: -60%
            width: 150%

@media screen and (max-width: 1400px)
  #movie-store
    .view-box
      .content-box
        width: 60%
        margin: 200px auto
        display: flex

@media screen and (max-width: 450px)
  @media (orientation: portrait) //  , landscape 
    #movie-store
      p
        width: 85%
      .top-flow-img-box
        img
          width: 80%
      .view-box
        .content-box
          width: 90%
          margin: 150px auto
          .img-box
            width: 60%
      .view-box.bigger
        .content-box
          .img-box
            width: 165%
</style>



