<template lang="pug">
  #miss-q
    .img-box
      img(
        :src="require('@/assets/images/works_img/griphic/miss_Q/0.png')"
        )
    .start(ref="el_start")
      .bg-top
      .bg-bottom
      .text-box
        h2 
        h4 ,,,&&,logo,,,,,,
    .main
      el-row.one-el-row(
        type="flex"
        justify="space-between"
      )
        el-col.m-el-col(
          :xs="24"
          :sm="8"
          )
          img(
            :src="require('@/assets/images/works_img/griphic/miss_Q/2.png')"
            )
        el-col(
          :xs="24"
          :sm="16"
          )
          img(
            :src="require('@/assets/images/works_img/griphic/miss_Q/1.png')"
            )
          h4 Q~~,,
      el-row.two-el-row
        el-col(
          :xs="24"
          :sm="12"
          )
          h4 :<br><br>, M ,,,, M 
          el-row.row-in-row
            el-col
              img(
                :src="require('@/assets/images/works_img/griphic/miss_Q/4.png')"
                )
            el-col
              h4 ,,,,,
        el-col.padding-left(
          :xs="24"
          :sm="12"
          )
          img(
            :src="require('@/assets/images/works_img/griphic/miss_Q/3.png')"
            )
          h4 ,,
      el-row.card-el-row
        el-col
          img(
            :src="require('@/assets/images/works_img/griphic/miss_Q/5.png')"
            )
          img(
            :src="require('@/assets/images/works_img/griphic/miss_Q/6.png')"
            )
        el-col
          h4 ,,,
    el-row.bottom-el-row
      el-col
        img(
          :src="require('@/assets/images/works_img/griphic/miss_Q/7.png')"
          )

</template>

<script>

export default {
  name: 'missQ',
    components: {
   
    },
    data () {
    return {
      
    }
  },
  computed: {
    
  },
  mounted () {
    this.setELStartLayout()
    window.onresize = () => {
      this.setELStartLayout()
    }
  },
  methods: {
    setELStartLayout() {
      let elTextBoxH = null
      let elStartChildren = [...this.$refs.el_start.children]
      let elTextBox = elStartChildren.find((el) => {
        return el.className === 'text-box'
      })
      if(elTextBox) {
        elTextBoxH = elTextBox.clientHeight

      }
      if(elTextBoxH) {
        this.$refs.el_start.style.height = `${elTextBoxH * 3}px `
        for(let i = 0;i < 2;i++) {
          elStartChildren[i].style.height = `${elTextBoxH * 1.8}px`
        }
      }

    }
  },
  watch: {

  }
}
</script>

<style lang="sass">

$shareSkew: 3deg
$m-el-col-top: 20px

@mixin skew($deg)
  -moz-transform: skewY($deg)
  -webkit-transform: skewY($deg)
  -o-transform: skewY($deg)
  -ms-transform: skewY($deg)

#miss-q
  letter-spacing: 3px
  .img-box
    padding: 100px 0px
    text-align: center
    img
      width: 250px
  .start
    position: relative
    display: flex
    align-items: center
    justify-content: center
    color: #ffffff
    div
      position: absolute
    .bg-top
      background-color: #a97517
      width: 100%
      @include skew($shareSkew)
    .bg-bottom
      background-color: #B19263
      opacity: 0.7
      width: 100%
      @include skew(-$shareSkew)
    .text-box
      display: flex
      flex-flow: column nowrap
      align-items: center
      width: 45%
      h2
        width: 100%
      h4
        width: 100%
  .main
    width: 80%
    margin: 50px auto
    img
      width: 150px
    h4
      margin-top: 20px
    .m-el-col
      display: flex
      align-items: flex-end
      img
        position: relative
        top: $m-el-col-top
    .two-el-row
      margin-top: $m-el-col-top
      display: flex
      align-items: center
      h4
        width: 80%
      .padding-left
        padding-left: 20px
      .row-in-row
        margin-top: 20px
    .card-el-row
      text-align: center
      margin-top: 50px
      img
        width: 25%
      h4
        text-align: left
  .bottom-el-row
    margin: 100px 0px
    img
      width: 100%

@media screen and (max-width: 1400px)
  #miss-q
    .img-box
      padding: 100px 0px 50px
      text-align: center
      img
    .start
      .text-box
        width: 60%
    .main
      .card-el-row
        img
          width: 35%

@media screen and (max-width: 450px)
  @media (orientation: portrait) //  , landscape 
    #miss-q
      .img-box
        padding: 80px 0px
        img
          width: 180px
      .start
        margin-top: -80px
        div
        .bg-top
          @include skew(10deg)
        .bg-bottom
          @include skew(-10deg)
        .text-box
          width: 90%
      .main
        .el-row.one-el-row.el-row--flex
          display: flex
          flex-direction: column-reverse
        .two-el-row
          display: block
          h4
            width: 100%
          .padding-left
            padding-left: unset
            padding-top: 20px
        .card-el-row
          img
            width: 50%
  
</style>



